import { PageProps } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';
import React, { FC } from 'react';
import * as style from './index.module.scss';
import Layout from '../../components/Layout';
import SEO from '../../components/SEO';

const ContactPage = () => {
    return (
        <Layout>
            <div
                className={style.div}
            >
                <section>
                    <h2>Want to communicate with me?</h2>
                    <p>The best way to get in touch with me is email. Feel free to send me any emails, or maybe you just want to say hi.</p>
                </section>
                <nav>
                    <a
                        id={style.linkEmail}
                        className={style.link}
                        rel="noreferrer noopener"
                        href="mailto:contact@SNinjo.com"
                    >
                        <p>Email</p>
                        <picture
                            id={style.email}
                            className={style.icon}
                        >
                            <StaticImage
                                src='../../assets/icon/email.png'
                                alt='email logo'
                                loading='lazy'
                            />
                        </picture>
                    </a>
                    <a
                        id={style.linkLinkedin}
                        className={style.link}
                        target="_blank"
                        rel="noreferrer noopener"
                        href="https://www.linkedin.com/in/ru-jun-liao-3bba07196/"
                    >
                        <p>Linkedin</p>
                        <picture
                            id={style.linkedin}
                            className={style.icon}
                        >
                            <StaticImage
                                src='../../assets/icon/linkedin.png'
                                alt='linkedin logo'
                                loading='lazy'
                            />
                        </picture>
                    </a>
                    <a
                        id={style.linkInstagram}
                        className={style.link}
                        target="_blank"
                        rel="noreferrer noopener"
                        href="https://www.instagram.com/sninjo657/"
                    >
                        <p>Instagram</p>
                        <picture
                            id={style.instagram}
                            className={style.icon}
                        >
                            <StaticImage
                                src='../../assets/icon/instagram.png'
                                alt='instagram logo'
                                loading='lazy'
                            />
                        </picture>
                    </a>
                </nav>
            </div>
        </Layout>
    )
}
export default ContactPage;

export const Head: FC<PageProps> = ({ location }) => (
    <SEO
        title='Contact'
        pathname={location.pathname}
    />
)
